    emit("submit");
    close();
  };
  return {
    TUITranslateService,
    isPC,
    isH5,
    isUniFrameWork,
    clickType,
    showDialog,
    isHeaderShowDialog,
    isFooterShowDialog,
    backgroundDialog,
    showTitle,
    toggleView,
    close,
    submit
  };
};
export default __sfc_main;
